<h1>populate.js</h1>

<p>Simple script that builds JS-model based on data retrieved from HTML</p>

<div class="p" style="font-size:16px;line-height:24px;">
    <code>GIT: </code><a target="_blank" href="https://github.com/maxcoredev/maxcoredev.github.io/blob/master/libs/populate.js">https://github.com/maxcoredev/maxcoredev.github.io/blob/master/libs/populate.js</a><br>
    <code>CDN: </code><a target="_blank" href="https://cdn.jsdelivr.net/gh/maxcoredev/maxcoredev.github.io@master/libs/populate.js">https://cdn.jsdelivr.net/gh/maxcoredev/maxcoredev.github.io@master/libs/populate.js</a>
</div>

<h2>How it works</h2>

<p>Just describe JS-model and it will be populated with data from HTML</p>

<pre><code class="html">{`<!DOCTYPE html>
<html>
<head></head>
<body>

    <span data-model="user.name">John</span>
    <span data-model="user.balance">0</span>

    <script src="https://cdn.jsdelivr.net/gh/maxcoredev/maxcoredev.github.io@master/libs/populate.js"></script>

    <script>
    const user = new Model('user', {
        name: 'string',
        balance: 'number',
    });
    </script>

</body>
</html>`}</code></pre>

<p>Now, global object <b>user</b> can be easily managed (UI will be instantly synced):</p>
<pre><code class="javascript">{`>>> user.name
'John'
>>> user.balance = 10
10`}</code></pre>

<h2>Example</h2>

<div class="form p">

	<div style="display:flex;">

		<div style="margin-right:24px;">
		    <b>Balance</b>
		    <span data-model="user.balance">0</span>
		    <!-- <span data-hidden="user.balance > 0" style="color:green">Please, add some balance to hide this message</span> -->
			<b>Add balance</b>
		    <button onclick="user.changeBalance(10)">Add +10</button>
		    <button onclick="user.changeBalance(-10)">Subtract -10</button>
		</div>

		<div style="margin-right:24px;">
	    	<b>Name</b>
	    	<span data-model="user.name">John</span>
			<b>Change name</b>
			<input type="text" data-model="user.name" value="John" style="width:240px;">
		</div>

	</div>

</div>

<pre><code class="html">{`<b>Balance</b>
<span data-model="user.balance">0</span>

<b>Add balance</b>
<button onclick="user.changeBalance(10)">Add +10</button>
<button onclick="user.changeBalance(-10)">Subtract -10</button>

<b>Name</b>
<span data-model="user.name">John</span>

<b>Change name</b>
<input type="text" data-model="user.name" value="John">

<script>
    const user = new Model('user', {
            name: 'string',
            balance: 'number',
        }, {
            changeBalance(amount) {
                this.balance += amount
            }
        }
    );
</script>`}</code></pre>

