<script>

	import { afterUpdate } from 'svelte';

	import Home from './Home.svelte';
	import Buttons from './Buttons.svelte';
	import Populate from './Populate.svelte';
	import Select from './Select.svelte';
	import Table from './Table.svelte';
	import Omgdrop from './Omgdrop.svelte';

	let component;

	let hash;

	function route() {
		hash = location.hash;
		if (hash === '') {
			component = Home;
		} else if (hash === '#buttons') {
			component = Buttons;
		} else if (hash === '#populate') {
			component = Populate;
		} else if (hash === '#select') {
			component = Select;
		} else if (hash === '#omgdrop') {
			component = Omgdrop;
		}
	}

	afterUpdate(()=>{
		hljs.initHighlighting.called = false;
		hljs.initHighlighting();
	})

	route();

	function goto() {
		window.scroll(0,0);
		route();
	}

	window.addEventListener('hashchange', goto);

	let out = '<svg viewBox="0 0 24 24"><path d="M10.09 15.59L11.5 17l5-5-5-5-1.41 1.41L12.67 11H3v2h9.67l-2.58 2.59zM19 3H5c-1.11 0-2 .9-2 2v4h2V5h14v14H5v-4H3v4c0 1.1.89 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2z"/></svg>';

</script>

<style>
	.header {background:#fff;z-index:1;box-shadow:0 1px 0 var(--border-color);--header-height:54px;line-height:var(--header-height);display:flex;position:sticky;top:0;}
		.header a {color:#000;}
		.header a:hover {text-decoration:none;}

		.logo {font-size:24px;font-weight:var(--bold);padding:0 24px;}

		.item {padding:0 24px;font-size:var(--font-size-small);font-weight:var(--bold);display:flex;align-items:center;}
		.item:hover   {background:var(--hover);box-shadow:0 1px 0 var(--border-color);}
		.item:active  {background:var(--active);}
		.item.current {background:var(--current);box-shadow:0 1px 0 var(--border-color);}
		.item :global(svg) {height:24px;margin-left:12px;}

	.content {flex:1;}

    .footer {background:#fff;box-shadow:0 -1px 0 var(--border-color);padding:24px 0;position:relative;}
        .copyright {margin-left:24px;}
</style>

<header class="header">
	<a class="logo" class:current="{hash === '/about'}" href="/">maxcoredev</a>
	<a class="item" class:current="{hash === '#buttons'}" href="/#buttons" style="margin-left:auto;">buttons.css</a>
	<a class="item" class:current="{hash === '#populate'}" href="/#populate">populate.js</a>
	<a class="item" class:current="{hash === '#select'}" href="/#select">select.js</a>
	<a class="item" href="https://github.com/maxcoredev?tab=repositories" target="_blank">GitHub {@html out}</a>
</header>

<main>
	<svelte:component this={component}/>
</main>

<footer class="footer">
    <div class="copyright">
        © {@html new Date().getFullYear()}
    </div>
</footer>

