<style>
	.buttons {display:flex;flex-wrap:wrap;align-items:flex-end;margin-top:24px;margin-bottom:24px;}
	.buttons.dark {padding-top:36px;padding-bottom:24px;background:#282c34;margin-bottom:0;}
		.buttons > * {margin:0 24px 12px 0;}
		.buttons > *:last-child {margin-right:0;}
</style>

<h1>buttons.css</h1>

<p style="font-size:18px;line-height:27px;">
    <code>GIT: </code><a target="_blank" href="https://github.com/maxcoredev/maxcoredev.github.io/blob/master/buttons.css">https://github.com/maxcoredev/maxcoredev.github.io/blob/master/buttons.css</a><br>
    <code>CDN: </code><a target="_blank" href="https://cdn.jsdelivr.net/gh/maxcoredev/maxcoredev.github.io@master/buttons.css">https://cdn.jsdelivr.net/gh/maxcoredev/maxcoredev.github.io@master/buttons.css</a>
</p>

<h2>Classes</h2>

<pre><code class="html">
{`<button [disabled] class="button
  [large|big|regular|medium|small|tiny]
  [red|green|blue]
  [outlined]
  [pending]
  [icon-left|icon-right]
  [on-dark]">Button</button>`}
</code></pre>

<h2>States</h2>

<p class="buttons">
	<button class="button">Default</button>
	<button class="current button">Current</button>
	<button disabled class="button">Disabled</button>
	<button disabled class="pending button">Pending</button>
</p>

<h2>Sizes</h2>

<p class="buttons">
	<button class="large button">Large</button>
	<button class="big button">Big</button>
	<button class="regular button">Regular</button>
	<button class="medium button">Medium</button>
	<button class="small button">Small</button>
	<button class="tiny button">Tiny</button>
</p>
<p class="buttons">
	<button class="blue large button">Large</button>
	<button class="blue big button">Big</button>
	<button class="blue regular button">Regular</button>
	<button class="blue medium button">Medium</button>
	<button class="blue small button">Small</button>
	<button class="blue tiny button">Tiny</button>
</p>

<h2>Icons</h2>

<p class="buttons">
	<button class="button icon-left">{@html i}Default</button>
	<button class="blue button icon-left">{@html i}Colored</button>
	<button class="outlined blue button icon-left">{@html i}Outlined</button>
</p>
<p class="buttons">
    <button class="button icon-right">Default{@html i}</button>
	<button class="blue button icon-right">Colored{@html i}</button>
	<button class="outlined blue button icon-right">Outlined{@html i}</button>
</p>

<h2>Styles</h2>

<p class="buttons">
	<button class="red button">Red</button>
	<button class="green button">Green</button>
	<button class="blue button">Blue</button>

    <button class="icon-left red button">{@html i}Icon</button>
	<button class="green button" disabled>Disabled</button>
	<button class="blue button pending" disabled>Pending</button>
</p>

<p class="buttons">
    <button class="outlined red button">Red</button>
	<button class="outlined green button">Green</button>
	<button class="outlined blue button">Blue</button>
    <button class="outlined icon-left red button">{@html i}Icon</button>
	<button class="outlined green button" disabled>Disabled</button>
	<button class="outlined blue button pending" disabled>Pending</button>
</p>

<p class="buttons dark">
	<button class="on-dark red button">Red</button>
	<button class="on-dark green button">Green</button>
	<button class="on-dark blue button">Blue</button>

    <button class="on-dark icon-left red button">{@html i}Icon</button>
	<button class="on-dark green button" disabled>Disabled</button>
	<button class="on-dark blue button pending" disabled>Pending</button>
</p>

<p class="buttons dark" style="margin-top:0;padding-top:0;">
	<button class="outlined on-dark red button">Red</button>
	<button class="outlined on-dark green button">Green</button>
	<button class="outlined on-dark blue button">Blue</button>

    <button class="outlined on-dark icon-left red button">{@html i}Icon</button>
	<button class="outlined on-dark green button" disabled>Disabled</button>
	<button class="outlined on-dark blue button pending" disabled>Pending</button>
</p>
